header {
  height: 100px;
  background: linear-gradient(4deg, rgba(9, 111, 121, .33) 0%, rgba(24, 137, 209, .824) 29%, rgba(0, 198, 255, .298) 100%);
}

.ss-main {
  background-color: rgba(38, 59, 206, 0);
  border: 3px solid rgba(22, 72, 172, .46);
}

button, input {
  background-color: rgba(22, 72, 172, 0);
  border: 3px solid rgba(22, 72, 172, .46);
}

body {
  background: rgba(24, 138, 209, .11);
  margin: 0 auto;
}

canvas {
  height: 350px;
  width: 600px;
}

.main-div {
  max-width: 1000px;
  margin: 20px auto;
}

canvas {
  width: 1200px;
}

.flatpickr-days:not(.flatpickr-day) {
  background-color: #89b4f9;
}

.flatpickr-day {
  opacity: 1;
}

.flatpickr-day:nth-child(7n+1), .flatpickr-day:nth-child(7n+7) {
  color: red;
}

.flatpickr-calendar .flatpickr-day.prevMonthDay {
  color: rgba(57, 57, 57, .3);
}

.flatpickr-day.flatpickr-disabled, .flatpickr-day.nextMonthDay {
  color: rgba(57, 57, 57, .1);
}

.tickers-div {
  display: flex;
}

.mainNews {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
  position: relative;
}

.newsDiv {
  width: 465px;
  height: 70vh;
  margin-bottom: 20px;
  padding-left: 14px;
  position: relative;
  overflow-x: hidden;
  overflow-y: scroll;
  box-shadow: 3px -1px 14px rgba(0, 0, 0, .1), 0 1px 3px rgba(0, 0, 0, .08);
}

.newsDiv:nth-child(2n+1):before {
  content: "";
  width: 6px;
  background-color: rgba(51, 51, 230, .435);
  border-radius: 50%;
  margin-left: 15px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -3%;
}

.newsDiv::-webkit-scrollbar {
  width: 5px;
  background-color: rgba(51, 51, 230, .435);
  border-radius: 50%;
}

.newsDiv::-webkit-scrollbar-thumb {
  border-top: 20px solid #001aff;
  border-radius: 10%;
}

.news-paragraph {
  padding-bottom: 10px;
}

.div-afterp {
  min-height: 10px;
}

@media screen and (max-width: 420px) {
  .main-div {
    max-width: 350px;
    margin: auto 20px;
  }

  .tickers-div {
    max-width: 350px;
    flex-direction: column;
    align-items: center;
    padding-bottom: 15px;
  }

  .select1 {
    width: 320px;
    margin-bottom: 15px;
    font-size: 20px;
  }

  .select2 {
    width: 320px;
    font-size: 20px;
  }

  .buttons-div {
    max-width: 350px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    display: flex;
  }

  canvas {
    height: 200px;
    width: 320px;
  }
}

